@import "../../../sass/consts";

.museum_edit_page {
  background-color: $bg8;
  padding-bottom: 2rem;

  .item {
    .label {
      line-height: 2rem;
    }

    margin-bottom: .5rem;
    padding: 0 .3rem;

    .logo {
      width: 3rem;
      height: 3rem;

      .am-image-picker-list {
        padding: 0;
        margin: .2rem 0 0;
      }
    }

    .imgs {
      padding-bottom: .5rem;

      .am-image-picker-list {
        margin-bottom: 0;
      }
    }
  }

  .base_info {
    .am-input-control input {
      text-align: right;
    }
  }

  .certificate {
    .tips {
      color: $main2;
    }

    .example_img {
      width: 100%;
    }
  }

  .state1 {
    line-height: 3rem;
    border-radius: 1.5rem;
    margin: 2rem 10%;
    text-align: center;
  }
}
